.main-btn-link {
  position: relative;
  display: inline-block;
  padding: 41px 58px 48px 60px;
  color: #fff;
  font-family: "OpenSans", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  line-height: 120%;
  text-align: center;
  will-change: transform;
  transition: transform 0.4s cubic-bezier(0.5, 3, 0, 1);
}

.main-btn-link::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 50%;
  z-index: -1;
  width: calc(100% - 15px);
  height: 100%;
  background-color: #002ea4;
  box-shadow: 0px 30px 100px rgba(0, 71, 255, 0.4);
  border-radius: 150px;
  transform: translateX(-50%);
}

.main-btn-link::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: linear-gradient(91.74deg, #0047ff -3.93%, #0400cd 95.4%), #0047ff;
  box-shadow: inset 0px 0px 10px rgba(255, 255, 255, 0.2);
  border-radius: 70px;
}

.main-btn-link--small {
  padding: 22px 71px 21px;
  font-size: 20px;
}

.main-btn-link--small::before {
  top: 3px;
  width: calc(100% - 6px);
}

.main-btn-link--small::after {
  background: linear-gradient(91.68deg, #0047ff 23.26%, #0033b7 167.11%), #0047ff;
  box-shadow: inset 0px 0px 10px rgba(255, 255, 255, 0.2);
}

@media (min-width: 767.98px) {
  .main-btn-link:hover {
    transform: scale(1.05, 1.05);
    transition: transform 0.4s cubic-bezier(0.5, 3, 0, 1);
  }
  .main-btn-link:focus {
    transform: scale(1.05, 1.05);
    transition: transform 0.4s cubic-bezier(0.5, 3, 0, 1);
  }
}

@media (max-width: 767.98px) {
  .main-btn-link {
    padding: 22px 23px;
    font-size: 15px;
    line-height: 20px;
  }
  .main-btn-link::before {
    content: "";
    position: absolute;
    top: 3px;
    left: 50%;
    z-index: -1;
    width: calc(100% - 6px);
  }
  .main-btn-link::after {
    background: linear-gradient(91.74deg, #0047ff -3.93%, #0400cd 95.4%), #0047ff;
    box-shadow: inset 0px 0px 10px rgba(255, 255, 255, 0.2);
  }
  .main-btn-link--small {
    padding: 15px 56px;
    line-height: 18px;
  }
  .main-btn-link--small::after {
    background: linear-gradient(91.68deg, #0047ff 23.26%, #0033b7 167.11%), #0047ff;
    box-shadow: inset 0px 0px 10px rgba(255, 255, 255, 0.2);
  }
}

